import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  forwardRef,
  Input,
  Renderer2,
  ViewChild,
} from '@angular/core';
import { InputBaseComponent } from '@backbase/ui-ang/base-classes';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { DomAttributesService } from '@backbase/ui-ang/services';
import { DeprecationsService } from '@backbase/foundation-ang/future';
import { NgSelectComponent } from '@ng-select/ng-select';

/**
 * @name InputPhoneComponent
 *
 * @description
 * Component that displays a phone input.
 *
 * @a11y Current component provide option to pass needed accessibility
 * attributes. You need to take care of properties that are required in your case :
 *  - role
 *  - aria-activedescendant
 *  - aria-describedby
 *  - aria-expanded
 *  - aria-invalid
 *  - aria-label
 *  - aria-labelledby
 *  - aria-owns
 *
 * ariaLabel is discernible text for phone input
 */
export interface CountryData {
  countryCode: string;
  isoCode: string;
  countryName: string;
}
export type CountryList = Array<CountryData>;

@Component({
  selector: 'bb-input-phone-ui',
  templateUrl: './input-phone.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => InputPhoneComponent),
      multi: true,
    },
  ],
})
export class InputPhoneComponent extends InputBaseComponent implements AfterViewInit {
  /**
   * The placeholder for the phone input. Defaults to an empty string;
   */
  @Input() placeholder = '';

  /**
   * The maxLength for the phone input.
   */
  @Input() maxLength: number | undefined;

  /**
   * The minLength for the phone input.
   */
  @Input() minLength: number | undefined;

  /**
   * Whether the phone input is readonly.
   */
  @Input() readonly = false;

  /**
   * Aria label for the phone input
   */
  @Input()
  ariaLabel = 'Phone Input';

  /**
   * The autocomplete value of the enclosed input control.
   */
  @Input() autocomplete: 'tel' | 'on' | 'off' | undefined;
  /**
   * List of countries with its code ,iso code and flag
   */
  @Input() countryList: CountryList | undefined;
  /**
   * Dropdown Position (bottom | top | auto)
   */
  @Input() dropdownPosition: 'bottom' | 'top' | 'auto' | undefined;
  /**
   * Allow to clear selected value.
   */
  @Input() clearable = false;
  /**
   * Allow to search for value.
   */
  @Input() searchable = false;
  /**
   * Default country selected for country dropdown.
   */
  @Input() defaultCountryIsoCode: string | undefined;
  /**
   * Enable/Disable Country code dropdown
   */
  @Input() enableCountryCode = false;

  @ViewChild('inputPhone') inputEl: ElementRef | undefined;
  /**
   * ng-select
   */
  @ViewChild(NgSelectComponent) ngSelect: NgSelectComponent | undefined;

  defaultCountryCode: string | undefined;
  defaultCountryName: string | undefined;
  countryDropdowndisabled: boolean | undefined;
  resultCount = 0;
  private readonly _statusId = this.domAttrService.generateId();

  get statusId() {
    return this._statusId;
  }

  constructor(
    private readonly domAttrService: DomAttributesService,
    private readonly elem: ElementRef,
    private readonly renderer2: Renderer2,
    protected readonly cd: ChangeDetectorRef,
    protected readonly deprecationsService: DeprecationsService,
  ) {
    super(cd, deprecationsService);
  }

  private constructCountryData() {
    if (this.enableCountryCode && this.countryList) {
      this.countryDropdowndisabled = this.countryList.length === 1;
      if (!this.countryDropdowndisabled) {
        const defaultCountry = this.countryList.find((city) => city.isoCode === this.defaultCountryIsoCode);
        this.defaultCountryCode = defaultCountry?.countryCode;
        this.defaultCountryName = defaultCountry?.countryName;
      } else {
        this.defaultCountryCode = this.countryList[0].countryCode;
        this.defaultCountryName = this.countryList[0].countryName;
      }
    }
  }

  ngAfterViewInit(): void {
    if (!this.ariaLabel) {
      this.domAttrService.moveAriaAttributes(
        this.elem.nativeElement,
        this.inputEl && this.inputEl.nativeElement,
        this.renderer2,
      );
    }
    this.constructCountryData();
  }

  onChanges(event: CountryData) {
    this.defaultCountryName = event.countryName;
  }

  onClose() {
    if (this.ngSelect && !this.ngSelect.isOpen) {
      this.ngSelect.constructor.prototype.focus.call(this.ngSelect);
    }
  }

  onClear() {
    this.onFilterChange(undefined);
  }

  onFilterChange(query: string | undefined): void {
    if (this.ngSelect) {
      this.ngSelect.filter(query || '');
    }
    this.updateResultCount();
  }

  private updateResultCount(): void {
    this.resultCount = this.ngSelect?.itemsList.filteredItems.filter(({ children }) => !children).length || 0;
  }

  searchFunc(term: string, item: CountryData): boolean {
    term = term.toLocaleLowerCase();

    return (
      item.countryCode.toLocaleLowerCase().indexOf(term) > -1 ||
      item.countryName.toLocaleLowerCase().indexOf(term) > -1 ||
      item.isoCode.toLocaleLowerCase().indexOf(term) > -1
    );
  }
}
